@import url(./variables.css);
.App {
  text-align: center;
}


/* width */
::-webkit-scrollbar {
  width: 10px;
  background: black;
}

/* Track */
::-webkit-scrollbar-track {
  background: #111;
  box-shadow: inset 0 0 5px #111111; 
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #212121; 
  border-radius: 10px;
}


body {
  background-color: var(--ac-black);
  color: var(--ac-white);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.ui.segment{
  background-color: #000;
  padding: 0;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.nav-container{
  /* width: 60%; */
  display: flex;
  justify-content: center;
  margin: auto;
  background-color: var(--ac-gray);
}
.floatright
{
  float: right;
  width: 1000px;
}

.floatleft {
  float: left;
  width: 10px;
}

.white {fill:var(--ac-white);}

#welcome {
  font-size: 30px;
  margin: 0;
  color: var(--ac-primary);
}
.intro{
  display: flex;
  justify-content: center;
  color: var(--ac-text-gray);
}

#intro{
  font-size: 24px;
  margin: 0%;
}

#portfolio{
  font-size: 24px;
  color: var(--ac-primary);
  margin: 0%;
}

.red-text {
  color: var(--ac-primary);
}

.gray-text {
  color: var(--ac-text-gray);
}

a {
  text-decoration: none;
}

.tabs{
  margin: 10px;
}